import './App.css';
import Clarifai from 'clarifai';
import FaceRecognition from './components/FaceRecognition/FaceRecognition';
import ImageLinkForm from './components/ImageLinkForm/ImageLinkForm';
import React, { useState } from "react";

function App() {

  const imageUrl = 'https://d1hb67vd7r4m10.cloudfront.net/images/easyblog_articles/89/b2ap3_thumbnail_ee72093c-3c01-433a-8d25-701cca06c975.jpg';

  const [state, setState] = useState(
    {
      input: '',
      box: {},

      // route: 'signin'
    }
  );



  let calculateFaceLocation = (data) => {
    const jsonData = JSON.parse(data);
    // console.log(jsonData.outputs[0].data.regions[0].region_info.bounding_box);
    const clarifaiFace = jsonData.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('inputImage');
    const width = Number(image.width);
    const height = Number(image.height);
    return {
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - (clarifaiFace.right_col * width),
      bottomRow: height - (clarifaiFace.bottom_row * height)
    }
  }
  const displayFaceBox = (box) => {
    console.log("box:", box); // check if box is valid
    setState({ ...state, box: box });
  }


  const onButtonSubmit = () => {

    //###################### Configuring the CLARIFAI API #######################
    //https://docs.clarifai.com/api-guide/predict/images

    // Your PAT (Personal Access Token) can be found in the portal under Authentification
    const PAT = 'b200e1646d5c4d72aaa1bc2a5aa2225b';
    const USER_ID = 'rlcastro';
    const APP_ID = 'facedet';
    const MODEL_ID = 'face-detection';
    const MODEL_VERSION_ID = '45fb9a671625463fa646c3523a3087d5';

    let raw = JSON.stringify({
      "user_app_id": {
        "user_id": USER_ID,
        "app_id": APP_ID
      },
      "inputs": [
        {
          "data": {
            "image": {
              "url": state.input
            }
          }
        }
      ]
    });

    const requestOptions = {
      method: 'POST',
      headers: {
        'Accept': 'application/json',
        'Authorization': 'Key ' + PAT
      },
      body: raw
    };

    fetch("https://api.clarifai.com/v2/models/" + MODEL_ID + "/versions/" + MODEL_VERSION_ID + "/outputs", requestOptions)
      .then(response => response.text())
      .then(result => calculateFaceLocation(result))
      .then(result => displayFaceBox(result))
      .catch(error => console.log('error', error));
  }

  const onInputChange = (e) => {
    setState({ ...state,  input: e.target.value });
  }

  return (

    <div className="App">

      <ImageLinkForm onInputChange={onInputChange} onButtonSubmit={onButtonSubmit} />
      {/* <ImageLinkForm onInputChange={onInputChange}  /> */}
      <FaceRecognition box={state.box} imageUrl={state.input} />
      {/* <FaceRecognition imageUrl={state.input} /> */}

    </div>
  );
}

export default App;
